* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background: #fcf6f5;
}

nav {
  text-align: center;
  height: 100px;
  margin-top: 20px;
}

.container {
  display: flex;
  flex-flow: row wrap;
  flex-basis: 80%;
  justify-content: center;
}

.card {
  width: 600px;
  height: 600px;
  margin: 0 20px 20px;
  background-color: #755139;
}

.card-image-container {
  width: 100%;
  height: 50%;
}

.card-image-container img {
  width: 100%;
  height: 100%;
}

.card-header {
  font-family: "Oswald", sans-serif;
  font-size: 1.5625em;
  font-weight: 800;
  padding-left: 10px;
  text-decoration: underline;
  color: #f2edd7;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.card-text {
  font-family: Open Sans;
  font-style: normal;
  font-size: 0.9375em;
  color: #f2edd7;
  text-align: center;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  max-height: 200px;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 80%;
  margin: 10px auto 0;
}

.view-btn-container {
  margin: 0 auto;
  width: 150px;
  height: 30px;
  background: #f2edd7;
  font-family: Open Sans;
  border-radius: 15px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  text-align: center;
  position: relative;
  bottom: -10px;
  transition: all 1s ease;
}

.view-btn-container:active {
  transform: translate(0px, 3px);
}

.view-btn-container:hover {
  background-color: white;
}

.view-btn {
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  text-decoration: none;
  color: #383838;
  line-height: 30px;
  padding: 5px 30px;
  transition: 1s ease-in;
}

.recipe-form {
  position: fixed;
  width: 350px;
  height: 50px;
  bottom: 100px;
  left: 50%;
  display: flex;
  flex-flow: row wrap;
}

.recipe-form img {
  position: absolute;
  left: 10px;
  top: 10px;
}

.recipe-form input {
  position: relative;
  left: -50%;
  width: 100%;
  border-radius: 15px;
  border: 5px solid #755139;
  height: 40px;
  outline: none;
  padding: 5px 50px;
  font-family: Open Sans;
  color: #383838;
  background: #f2edd7;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.recipe-form input:focus {
  border-color: #e05a00;
}

.recipe-form button {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  background: #755139;
  color: #f2edd7;
  padding: 5px 30px;
  border-radius: 15px;
  font-family: Open Sans;
  outline: none;
  border: 3px solid #f2edd7;
  margin: 10px auto 80px;
  cursor: pointer;
  position: relative;
  left: -50%;
}

.recipe-prompt h3 {
  font-family: "Oswald", sans-serif;
  font-size: 4em;
  color: #755139;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

@media only screen and (max-width: 1024px) {
  .recipe-prompt h3 {
    font-size: 3em;
  }
}

@media only screen and (max-width: 768px) {
  .recipe-prompt h3 {
    font-size: 2.5em;
  }
}

@media only screen and (max-width: 450px) {
  .card-header {
    font-size: 1.3em;
  }

  .card-text {
    font-size: 0.8em;
    max-height: 180px;
  }

  .recipe-prompt h3 {
    font-size: 1.5em;
  }
}

@media only screen and (max-width: 375px) {
  .recipe-form input {
    width: 85%;
    height: 40px;
  }

  .recipe-form button {
    margin: 10px 0 0 80px;
  }
}
